import {common} from "~/utils/api/url";
import mixins from "~/utils/mixins";
import appTemplateValidater from "~/utils/validater/appTemplateValidater";
import MySelect from "~/components/MySelect";
import {MessageError, MessageSuccess} from "~/utils/utils";

export default {
  components: {MySelect},
  mixins: [mixins,appTemplateValidater],
  data(){
    return {
      tableData: [],
      collegeList: [],
      appDataList: [],
      types: [],
      categorys: [],
      subTitle: '',
      collegeData: '',
      searchKey: '',
      searchApp: '',
      searchStatus: '',
      detailData: '',
      drawerVisible: false,
      dialogLoading: false,
      dialogApp: false,
      serverImg: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
      uploadFileUrl: '',
      form: {
        id: '',
        name: '',
        type: '',
        category: '',
        dept: []
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        searchKey: this.searchKey
      };
      this.$axios.get(common.server_form_template_list, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    nodeClick(data){
      this.collegeData = data.department_path;
      this.page = 1;
      this.init();
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    handleTypeChange(event, type){
      if (type == 1){
        this.searchApp = event;
      }else if (type == 2){
        this.searchStatus = event;
      }else if (type == 3){
        this.form.type = event;
      }else if (type == 4){
        this.form.category = event;
      }
    },
    handleCascaderChange(data){
      this.form.dept = data;
    },
    detailItem(event, item){
      this.detailData = item;
      this.drawerVisible = true;
    },
    uploadFileSuccess(res, file){
      if (res.code == 200){
        this.serverImg = res.data.url;
      }else {

      }
    },
    closeDrawerDialog(event){
      this.detailData = '';
      this.serverImg = '';
      this.form = {
        id: '',
        appName: '',
        type: '',
        category: '',
        dept: []
      };
      this.subTitle = "";
      this.resetCasadeSelector('SelectorCollege');
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
      this.drawerVisible = event;
    },
    cancelDrawDialog(){
      this.detailData = '';
      this.drawerVisible = false;
    },
    okAppDrawDialog(){
      let url = '';
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            id: this.form.id,
            appName: this.form.appName,
            type: this.form.type,
            category: this.form.category,
            dept: this.form.dept,
          };
          url = common.class_edit;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params).then(res => {
            if (res.data.code == 200){
              this.drawerVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    }
  }
}
