import {oneOf} from "../utils/utils";
export default {
  name: 'mySelect',
  props: {
    placeholder: {
      default: '',
      type: String
    },
    selValue: {
      default: '',
      type: [String, Number,Boolean]
    },
    options: {
      default: function () {
        return [];
      },
      type: Array
    },
    size: {
      defalult: 'medium',
      validate: function (val) {
        return oneOf(val, ['large', 'medium', 'small', 'mini']);
      }
    },
    clearable: {
      default: false,
      type: Boolean
    },
    disabled: {
      default: false,
      type: Boolean
    },
    filterable: {
      default: false,
      type: Boolean
    },
    remote: {
      default: false,
      type: Boolean
    },
    loading: {
      default: false,
      type: Boolean
    },
    group: {
      default: false,
      type: Boolean
    },
    remoteMethod: {
      default: function (data) {
        return data;
      },
      type: Function
    },
    widthStyle: {
      default: '',
      type: [String, Number]
    }
  },
  computed: {
    selectModel(){
      this.value = this.selValue;
    },
    setPlaceholder(){
      this.selectPlaceholder = this.placeholder ? this.placeholder : this.$t("请选择")
    },
    setWidth(){
      if (!!this.widthStyle){
        this.width = {
          'width': this.widthStyle + "px"
        };
      }
    }
  },
  data() {
    return {
      selectPlaceholder: '',
      value: '',
      width: ''
    }
  },
  methods: {
    handleChange(data) {
      this.$emit('change', data);
    }
  }
}
